/* Styles pour ManageCategories */

.page-centered-container {
  max-width: 600px;
  margin: 0 auto;
  margin-top: 0;
  padding-top: 18px;
}

.page-centered-container h1 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  font-size: 2rem;
  font-weight: 700;
  color: #222;
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: center;
  margin-bottom: 24px;
}

.notification {
  margin: 10px auto;
  max-width: 400px;
  padding: 10px 18px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}

.notification.success {
  background: #e6ffe6;
  color: #1a7f1a;
}

.notification.error {
  background: #ffe6e6;
  color: #a33;
}

form {
  display: flex;
  gap: 10px;
  margin-bottom: 24px;
  align-items: center;
  justify-content: center;
}

form input[type="text"], form input[type="number"], form input {
  flex: 1;
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
}

form button[type="submit"] {
  padding: 8px 18px;
  border-radius: 6px;
  background: #1a7f1a;
  color: #fff;
  border: none;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 6px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
  cursor: pointer;
}

form button[type="submit"]:hover {
  background: #158a15;
}

.produits-table {
  width: 80%;
  max-width: 600px;
  margin: 0 auto;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  overflow: hidden;
}

.produits-table th, .produits-table td {
  /* border: 1px solid #e0e0e0; */
  border: none;
  padding: 10px 12px;
  text-align: center;
}

.produits-table td span i {
  margin-right: 7px;
}

.produits-table td input {
  padding: 6px 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 90%;
  font-size: 15px;
}

.produits-table td {
  border-bottom: 1px solid #eee;
}

.edit-button, .delete-button {
  border: none;
  border-radius: 6px;
  padding: 6px 12px;
  font-weight: 500;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
  cursor: pointer;
}

.edit-button {
  background: #007bff;
  color: #fff;
}

.edit-button:hover {
  background: #0056b3;
}

.delete-button {
  background: #c00;
  color: #fff;
}

.delete-button:hover {
  background: #a00;
}

.save-button {
  background: #1a7f1a;
  color: #fff;
}

.save-button:hover {
  background: #158a15;
}

.cancel-button {
  background: #eee;
  color: #333;
  border: none;
  border-radius: 6px;
  padding: 6px 12px;
  font-weight: 500;
  cursor: pointer;
}

.cancel-button:hover {
  background: #ddd;
}

.modal-bg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.18);
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
}

.confirm-modal {
  background: #fff;
  padding: 2em;
  border-radius: 10px;
  min-width: 260px;
  max-width: 340px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.13);
  position: relative;
  text-align: center;
}

.confirm-modal-title {
  font-size: 18px;
  margin-bottom: 18px;
}

.confirm-modal-actions {
  display: flex;
  gap: 16px;
  justify-content: center;
}

.confirm-modal .delete-button {
  padding: 8px 18px;
  font-weight: 600;
}

.confirm-modal .cancel-button {
  background: #eee;
  color: #333;
  border: none;
  border-radius: 6px;
  padding: 8px 18px;
  font-weight: 600;
  cursor: pointer;
}

.confirm-modal .cancel-button:hover {
  background: #ddd;
}

.icon-blue {
  color: #007bff;
}
.icon-lg {
  font-size: 1.75em;
}
.mr-8 {
  margin-right: 8px;
}
.mr-6 {
  margin-right: 6px;
}

.category-add-form-row {
  display: flex;
  gap: 8px;
  align-items: center;
  margin: 16px 0;
}
.category-add-input {
  width: 180px;
}

.category-row-form-row {
  display: flex;
  gap: 8px;
  align-items: center;
}
.category-row-input {
  width: 140px;
}

@media (max-width: 700px), (max-width: 600px) {
  /* Suppression du padding-top et margin-top sur .page-centered-container pour harmonisation UI */
  .page-centered-container h1 {
    font-size: 1.3rem;
    padding: 0.5em 0.5em;
  }
}

/* Responsive pour mobile ≤395px */
@media (max-width: 395px) {
  /* Champ d'édition centré avec largeur optimisée */
  .category-row-input {
    text-align: center;
    max-width: 150px !important;
    width: 150px !important;
  }
    /* Optimisation des colonnes du tableau avec plus d'espace pour les actions */
  .produits-table th:first-child,
  .produits-table td:first-child {
    width: 65% !important;
    max-width: 65% !important;
  }
  
  .produits-table th:last-child,
  .produits-table td:last-child {
    width: 35% !important;
    max-width: 35% !important;
  }
    /* Centrage et optimisation des boutons d'action */
  .actions-cell {
    gap: 6px !important;
    margin: auto !important;
    text-align: center !important;
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
  }
  
  /* Boutons compacts et optimisés - taille augmentée pour éviter la coupure */
  .actions-cell .edit-btn,
  .actions-cell .delete-btn,
  .actions-cell .save-btn,
  .actions-cell .cancel-btn {
    min-width: 32px !important;
    max-width: 32px !important;
    min-height: 32px !important;
    max-height: 32px !important;
    font-size: 1.1em !important;
    padding: 6px !important;
    margin: 1px !important;
    border-radius: 4px !important;
  }
  
  /* Réduction du padding global pour gagner de l'espace */
  .produits-table th, .produits-table td {
    padding: 8px 4px !important;
    font-size: 14px !important;
  }
}
